import React, { Component } from 'react';
import Paper from '@material-ui/core/Paper';
import { Divider } from '@material-ui/core';
import Badge from '@material-ui/core/Badge';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import { withStyles } from '@material-ui/core/styles';
import Medal from './Medal';

const styles = {
  country: {
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: 10,
    padding: 18,
    width: 260,
  },
  divider: {
    marginTop: 5,
    marginBottom: 10,
  },
  countryName: {
    fontSize: '1.8em',
    paddingRight: '8px',
  },
}

class Country extends Component {
  getMedalsTotal(country, medals) {
    let sum = 0;
    medals.forEach(medal => {
      sum += country[medal.name + 'MedalCount'];
    });
    return sum;
  }
  render() { 
    const { classes, country, medals, onIncrement, onDecrement, onDelete } = this.props;
    return (
      <Paper elevation={4} className={classes.country}>
        {/* <Badge badgeContent={ this.getMedalsTotal(country, medals) } color='primary'>
          <div className={classes.countryName}>
            { country.name }
          </div>
        </Badge> */}
        <Grid container spacing={0}>
        <Grid item xs={10}>
          <Badge badgeContent={ this.getMedalsTotal(country, medals) } color='primary'>
            <div className={classes.countryName}>
              { country.name }
            </div>
          </Badge>
        </Grid>
        <Grid item xs={2}>
          <IconButton aria-label="delete" onClick={() => onDelete(country.id)} style={{ padding: 6 }}>
            <DeleteIcon fontSize="medium" />
          </IconButton>
        </Grid>
      </Grid>
        <Divider className={classes.divider} />
        { medals.map(medal =>
          <Medal 
            key={ medal.id } 
            country={ country } 
            medal={ medal } 
            onIncrement={ onIncrement } 
            onDecrement={ onDecrement } />
        ) }
      </Paper>
    );
  }
}

export default withStyles(styles, { withTheme: true })(Country);
