import React, { Component } from 'react';
import Country from './components/Country';
import NewCountry from './components/NewCountry';
import { getMedals } from './services/medalService';
import { getCountries, addCountry, deleteCountry } from './services/countryService';
import Container from '@material-ui/core/Container';
import Badge from '@material-ui/core/Badge';
import { Grid } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import './App.css';

const styles = {
  appHeading: {
    textAlign: 'center',
    margin: 0,
    backgroundColor: '#BBBBF1',
    position: 'fixed',
    width: '100%',
    paddingTop: '15px',
    paddingBottom: '4px',
    zIndex: '1000',
    opacity: '.95',
  },
  appHeadingBadge: {
    fontSize: '2.0em',
    fontFamily: 'Teko, sans-serif',
  },
  appHeadingText: {
    paddingRight: '8px'
  },
}

class App extends Component {
  state = {
    countries: getCountries(),
    medals: getMedals(),
  }
  handleAdd = (name) => {
    addCountry(name);
    this.setState({ countries:getCountries() });
  }
  handleDelete = (countryId) => {
    deleteCountry(countryId);
    this.setState({ countries:getCountries() });
  }
  handleIncrement = (countryId, medal) => {
    const countries = [ ...this.state.countries ];
    const idx = countries.findIndex(c => c.id === countryId);
    countries[idx][medal + 'MedalCount'] += 1;
    this.setState({ countries: countries });
  }
  handleDecrement = (countryId, medal) => {
    const countries = [ ...this.state.countries ];
    const idx = countries.findIndex(c => c.id === countryId);
    if (countries[idx][medal + 'MedalCount'] === 0) return;
    countries[idx][medal + 'MedalCount'] -= 1;
    this.setState({ countries: countries });
  }
  getMedalTotal = (medal) => this.state.countries.reduce((a, b) => a + b[medal + 'MedalCount'], 0);
  getAllMedalsTotal() {
    let sum = 0;
    this.state.medals.forEach(medal => {
      sum += this.getMedalTotal(medal.name);
    });
    return sum;
  }
  render() { 
    const {classes} = this.props;
    const { countries, medals } = this.state;
    return (
      <React.Fragment>
        <div className={classes.appHeading}>
          <Badge 
            className={classes.appHeadingBadge}
            badgeContent={ this.getAllMedalsTotal() } 
            color='primary'>
            <div className={classes.appHeadingText}>Olympic Medals</div>
          </Badge>
        </div>
        <Container fixed={true} style={{paddingTop: '70px', paddingBottom: '20px'}}>
          <NewCountry onAdd={ this.handleAdd } />
          <Grid spacing={2} justifyContent='center' container>
            { countries.map(country =>
              <Grid item key={ country.id }>
                <Country 
                  country={country} 
                  medals={medals}
                  onDelete={ this.handleDelete }
                  onIncrement={ this.handleIncrement } 
                  onDecrement={ this.handleDecrement } />
              </Grid>
            )}
          </Grid>
        </Container>
      </React.Fragment>
    );
  }
}
 
export default withStyles(styles, { withTheme: true })(App);
