let countries = [
  { id: 1, name: 'United States', goldMedalCount: 0, silverMedalCount: 0, bronzeMedalCount: 0 },
  { id: 2, name: 'China', goldMedalCount: 0, silverMedalCount: 0, bronzeMedalCount: 0 },
  { id: 3, name: 'Germany', goldMedalCount: 0, silverMedalCount: 0, bronzeMedalCount: 0 },
  { id: 4, name: 'Spain', goldMedalCount: 0, silverMedalCount: 0, bronzeMedalCount: 0 },
]

export function getCountries() {
  return countries;
}

export function addCountry(name) {
  const id = countries.length === 0 ? 1 : Math.max(...countries.map(country => country.id)) + 1;
  countries = countries.concat({ id: id, name: name, goldMedalCount: 0, silverMedalCount: 0, bronzeMedalCount: 0 });
}

export function deleteCountry(countryId) {
  countries = countries.filter(c => c.id !== countryId);
}
